<template>
	<div class="privilege">
		<h3>支付保证金专享<i>易行汽车特权</i></h3>
            <ul>
                <li>
                <img :src='gift' />
                <p><label>购车礼包</label>现场订车专享易行汽车购车大礼包</p>
                </li>
                <li>
                <img :src='baozhang' />
                <p><label>低价保证</label>活动现场订车专享低价购车服务保障</p>
                </li>
            </ul>
	</div>
</template>

<script>
import pic1 from '@/assets/img/info/gift.png'
import pic2 from '@/assets/img/info/price.png'
export default{

	data(){
		return{
			gift: pic1,
			baozhang: pic2
		}
	}
}
</script>

<style scoped>
.privilege{
    background: #fff;
    position: relative;
    min-width: 300px;
    padding: 0rem 0.6rem;
    margin-top: 0.5rem;
}
.privilege>h3{
    font-size: 15px;
    text-align: center;
    padding: 5px 5px;
    color: #999;
    border-bottom: 1px solid #ededed;
    position: relative;
}
.privilege>h3>i{
    color: #ff0100;
    font-style: normal;
}
.privilege>ul{
    display: -webkit-box;
    background-color: #fff;
    color: #727272;
}
.privilege>ul>li{
    -webkit-box-flex: 1;
    box-flex: 1;
    width: 50%;
    font-size: 10px;
    margin: 10px 0px 5px;
    border-right: 1px dashed #e6e6e6;
    padding-left: 2%;
}
.privilege>ul>li>img{
    width: 36px;
    float: left;
    margin-right: 5px;
    margin-top: 10px;
}
.privilege>ul>li>p{
    margin-top: 2px;
    float: left;
    color: #BDBDBD;
    width: 69%;
}
.privilege>ul>li>p>label{
    font-size: 14px;
    display: block;
    color: #666;
}
/*car_privilege end*/
</style>


