<template>
	
	<div class="container">
		<lg_header></lg_header>
		<div class='password'>
		<el-form class='pas_wrap' :model='PasswordForm' :rules="rules" ref="PasswordForm" label-width="100px" status-icon>

			<el-form-item label=' ' prop="phone" class='pas_box'>
				<el-input type="text" placeholder="请输入手机号" v-model='PasswordForm.phone'></el-input>
				<el-button class="msgCode" :disabled='isDisabled' @click='msgCode'>{{buttonText}}</el-button>
			</el-form-item>

			<el-form-item label=' ' prop="code" class='pas_box'>
				<el-input type='text' placeholder="请输入短信验证码" v-model='PasswordForm.code'></el-input>
			</el-form-item>

			<el-form-item label=' ' prop="pass" class='pas_box'>
				<el-input type='password' placeholder="密码(6-20个数字或字符)" v-model='PasswordForm.pass'></el-input>
			</el-form-item>

			<el-form-item label=' ' prop="repass" class='pas_box'>
				<el-input type='password' placeholder="请确认密码" v-model='PasswordForm.repass'></el-input>
			</el-form-item>

			<el-form-item class='pas_btn'>
				<el-button class='pas_btnbutton' @click="pas_value('PasswordForm')">提交</el-button>
			</el-form-item>
			
		</el-form>

		<p class='message'>收不到短信？使用<a href=''>语音验证码</a></p>

		<div class='pas_serv'>
			<p>如有疑问请联系客服：<b>400-808-1710</b></p>
			<p>客服服务时间：周一至周五9:00-22:00</p>
		</div>
	</div>
	</div>
</template>



<style scoped>
.password{
	margin-top: 20px;
}
.pas_wrap{
	max-width: 640px;
	height: auto;
	position: relative;
	overflow: hidden;
	margin: 0 auto;
	text-align: center;
	padding: 16px 0;
}
.pas_wrap .pas_box{
	border-top: 1px solid #ededed;
    background-color: #fff;
    font-size: 16px;
    text-align: left;
    position: relative;
}

.pas_wrap .pas_box .msgCode{
	background-color: #f95554;
    position: absolute;
    right: 0;
    top: 0;
    display: inline-block;
    text-align: center;
    color: #fff;
    font-size: 14px;
    min-width: 85px;
    height: 40px;
    line-height: 10px;
}

.pas_wrap .pas_btn{
	margin: 20px auto 0;
	display: block;
}
.pas_wrap .pas_btn .pas_btnbutton{
	color: #fff;
    display: block;
    background-color: #f95554;
    padding: 10px 0;
    text-align: center;
    margin: 20px auto;
    font-size: 16px;
    border: 0;
    width: 90%;
    border-radius: 3px;
}
.password .message{
	text-align: center;
    margin: 7px;
    font-size: 13px;
}
.password .message>a{
	color: #f25252;
    text-decoration: underline;
}
.password .pas_serv{
	border: 1px solid #ccc;
    background: -webkit-gradient(linear, left top, left bottom, from(#fff), to(#f5f5f5));
    text-align: center;
    padding: 8px 0;
    line-height: 18px;
    margin: 30px 15px 10px;
    border-radius: 5px;
    color: #808080;
}
.password .pas_serv>p{
	font-size: 12px;
	line-height: 1.45rem;
}
.password .pas_serv>p>b{
	color: #f95554;
}
</style>


<style>
.pas_box .el-form-item__label{
	box-shadow: 1px 0px 5px rgba(0,0,0,0.1 );
    display: inline-block;
    width: 40px !important;
    height: 40px;
    vertical-align: bottom;
    text-align: center;
    font-size: 14px;
    color: #969696;
}
.pas_box .el-form-item__content{
	margin-left: 0 !important;
}
.pas_btn .el-form-item__content{
	margin-left: 0 !important;
}
.pas_box .el-input{
	width: 90%;	
}
.pas_box .el-input__inner{
	margin-left: 10px;
    border-left: 1px solid #e6e6e6;
    border: 0;
    width: 100%;
}
.pas_box .el-form-item__error{
	color: #fa5555;
    font-size: 12px;
    line-height: 1;
    padding-top: 4px;
    position: absolute;
    top: 97%;
    left: 13%;
}
.pas_box:first-child .el-input{
	width: 63%;
}
</style>

<script>
import lg_header from '@/components/lgComponents/Header'
export default{
	components: {lg_header},
	data(){
		var validatePhone = (rule, value, callback) =>{
       		if(value ===''){
       			callback('请输入手机号');
       		}else{
       			const reg = /^(((13[0-9]{1})|(14[4,7]{1})|(15[0-3,5-9]{1})|(17[0,1,3,5-8]{1})|(18[0-9]{1})|(19[8,9]{1})|(166))+\d{8})$/

       			if(reg.test(value)){
       				let mobile = {'mobile' : value};
       				console.log(mobile);
       				this.$http.post('https://www.yixingauto.com/groupon/login/checkmobile',mobile).then((res)=>{
       					if(res.body.code==200){
       						callback();
       					}else{
       						callback('手机号未注册');
       					}
       				});
       			}else{
       				return callback(new Error('请输入正确的手机号'));
       			}
       		}
       	};

       	var validateMsg = (rule, value, callback)=>{
       		if(value ===''){
       			callback('请输入短信验证码');
       		}else{
       			callback();
       		}
       	};

       	var validatePass = (rule, value, callback)=>{
       		if(value ===''){
       			callback('请输入密码');
       		}else{
       			callback();
       		}
       	};

       	var validateRepass = (rule, value, callback)=>{
       		if(value ===''){
       			callback('请再次输入密码');
       		}else if(value != this.PasswordForm.pass){
       			callback('两次输入密码不一致!');
       		}else{
       			callback();
       		}
       	};

		return{
			PasswordForm:{
				phone: '',
				code: '',
				pass: '',
				repass: ''
			},
			rules:{
				phone:[
				{validator: validatePhone, trigger: 'blur'}
				],
				code:[
				{validator: validateMsg, trigger: 'blur'}
				],
				pass:[
				{validator: validatePass, trigger: 'blur'}
				],
				repass:[
				{validator: validateRepass, trigger: 'blur'}
				]
			},
			buttonText: '发送验证码',
		    isDisabled: false, 
		    flag: true
		}
	},
	methods:{
		pas_value(formName){
			this.$refs[formName].validate((valid) =>{
				if(valid){
					let data = {'mobile':this.PasswordForm.phone,'pwd':this.PasswordForm.pass,'repwd':this.PasswordForm.repass,'verifycode':this.PasswordForm.code};
					this.$http.post('https://www.yixingauto.com/groupon/login/changepwd',data).then((res)=>{
						if(res.body.code==200){
							this.$router.push('/');
						}else{
						
						setTimeout(()=>{
      	 						this.$message({
      	 						type: 'error',
      	 						message: res.body.msg,
      	 						})
      	 					},500);
					}
					})
				}
			})
		},

		msgCode(){
			let codeData = {'mobile':this.PasswordForm.phone};
			this.$http.post('https://www.yixingauto.com/groupon/login/sendverifycode',codeData).then((res)=>{
				if(res.body.code==200){
					let time = 60
			        this.buttonText = '已发送'
			        this.isDisabled = true
			        if(this.flag){
			        	this.flag = false;
			        	let timer = setInterval(() =>{
			        		time--;
			        		this.buttonText = time + '秒'
			        		if(time === 0){
			        			clearInterval(timer);
			        			this.buttonText = '重新获取'
					            this.isDisabled = false
					            this.flag = true;
			        		}
			        	},1000)
			        }
				}else{
					console.log(res.body.msg);
				}
			})

		}

	}
}

</script>


