<template>
  <div class='header'>
  	<span class="back-btn" @click="goBack()">←</span>
  	<h6 class="header-name" v-text="title"></h6>
  </div>
</template>
<script>
 export default{
   data(){
   	  return{
   	  	title: '购车惠'
   	  }
   },
   methods:{
   	  goBack(){
   	  	 history.back(-1);
   	  }
   }
 }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .header{
	max-width: 640px;
	position: relative;
	background: -webkit-linear-gradient(left,#ee0c41,#fc3b3f);
	background: #ff3938;
	color: #fff;
	height: 2.55rem;
	line-height: 2.55rem;
}
.header h6{
	width: 70%;
	font-weight: bold;
	text-align: center;
	font-size: 1.1rem;
	letter-spacing: 1px;
	color: #fff;
}
.header span{
	display: block;
	width: 10%;
	float: left;
	text-align: center;
}
/*header end*/
</style>



