<template>
	<div class="full">
		<full_header></full_header>

		<div class="info">
			<dl>
                <dt><img :src='logo'></dt>
                <dd>
                    <h5>{{title}}</h5>
                    <p>需要支付保证金：<span>&yen;{{rmb}}</span></p>
                </dd>
            </dl>
		</div>

		<div class="logo">
			<el-form :model='fullForm' :rules="rules" ref="fullForm" label-width="100px" status-icon>

			<el-form-item label='姓名' prop="name" class='reg_box'>
				<el-input type="text" placeholder="请输入姓名" v-model='fullForm.name'></el-input>
			</el-form-item>

			<el-form-item label='手机' class='reg_box' prop='phone'>
				<el-input type="text" placeholder="请输入手机号" v-model='fullForm.phone'></el-input>
				<!-- <el-button @click="msgCode('phone')" class='msgCode'>获取验证码</el-button> -->
			</el-form-item>

			<el-form-item label='车型' prop="name" class='reg_box brand'>
				<el-radio v-model="brand_car" v-for='brand in barnds' :label='brand.id' border size="medium">{{brand.name}}</el-radio>
			</el-form-item>

			<!-- <el-form-item label='验证码' class='reg_box' prop='code'>
				<el-input type='text' placeholder="请输入短信验证码" v-model='fullForm.code'></el-input>
			</el-form-item> -->

			<!-- <el-form-item label='品牌' prop="name" class='reg_box'>
				<el-cascader :options="options" v-model="selectedOptions" @change="handleChange"></el-cascader>
			</el-form-item>	 -->

			<el-button type="danger" @click="Topay('fullForm')" class='btn'>提交并支付</el-button>		

			<!-- <el-form-item label=' ' class='reg_btn'>
				<el-button type="" @click="Topay('registerForm')" class='reg_btnbutton'>提交</el-button>
			</el-form-item> -->

			</el-form>
		</div>
		<!-- <p class="exit">已有账号，点击<span @click = 'Tologo'>登录</span></p> -->
		
		

	</div>
</template>



<script>
import full_header from '@/page/mobile/full/components/header'
import {setCookie,getCookie} from '@/assets/js/cookieUtil'
export default{
	components:{full_header},
	data(){

		var validateName = (rule, value, callback) =>{
       		if(value ===''){
       			callback('请输入姓名');
       		}else{
       			callback();
       		}
       	};

       	var validatePhone = (rule, value, callback) =>{
       		if(value ===''){
       			callback('请输入手机号');
       		}else{
       			const reg = /^(((13[0-9]{1})|(14[4,7]{1})|(15[0-3,5-9]{1})|(17[0,1,3,5-8]{1})|(18[0-9]{1})|(19[8,9]{1})|(166))+\d{8})$/
       			callback();
       		}
       		
       	};

       

		return{
			id: this.$route.query.id,
			logo: 'https://img.haoche.cn/logo/15.jpg',
			title: this.$route.query.title,
			rmb: this.$route.query.rmb,
			trade_no: '',//订单
			brand_car: '',
			barnds:[],
			fullForm:{
				name: '',
				phone: '',
				code: ''
			},
			rules:{
				name:[
				{validator: validateName, trigger: 'blur'}
				],
				phone:[
				{validator: validatePhone, trigger: 'blur'}
				]
			}
		}
	},
	created(){
        this.GetInfo();
    },
	methods:{
		GetInfo(){
			let data = {'groupbuy_id':this.id}
			this.$axios.post('https://www.yixingauto.com/groupon/Groupbuy/getcars',data).then((res)=>{
				let set = []
				if(res.data.code == 200){
					for(let i =0; i<res.data.data.length; i++){
						let groupinfo = {}
						groupinfo.id = res.data.data[i].id
						groupinfo.name = res.data.data[i].name
						set[i] = groupinfo
					}
				}
				this.barnds = set
				this.brand_car = set.id
			});
			
		},

		Topay(formName){
			this.$refs[formName].validate((valid)=>{
				if(valid){
					let data = {'groupbuyid':this.id,'mobile':this.fullForm.phone,'name':this.fullForm.name,'series_id': this.brand_car}
						this.$axios.post('https://www.yixingauto.com/groupon/Groupbuy/order',data).then((res) => {
							if(res.data.code == 200){
								this.trade_no = res.data.trade_no;
								this.$router.push({path:'/paymentList',query:{title:this.title, rmb: this.rmb, trade_no:this.trade_no}});
							}
						});
				}
			})
			
		},
		
		Tologo(){
			this.$router.push('/loginList');
		}
	},
	mounted () {
	    if (this.isLogin==undefined||this.isLogin=="") {
	      this.$router.replace('/loginList');
	    } else {
	      // 执行后续操作
	      this.fullForm.name = getCookie("name");
	      this.fullForm.phone = getCookie("phone");
	      }
  		},
	computed: {
    isLogin () {
      // return this.$store.getters.getLogin;
      this.fullForm.phone = getCookie("phone");
     // console.log(this.userId);
      return this.fullForm.phone;
    }
  },
}
</script>


<style scoped>
.full{
	max-width: 640px;
	margin: 0 auto;
	overflow: hidden;
	background: #f5f5f5;
}
.info{
	max-width: 640px;
	border-bottom: 2px dashed #e7e8ec;
	background: #fff;
	padding: 0px 10px 0px;
	margin: 10px 0;
	overflow: hidden;
	position: relative;
	
}
.info>dl{
    padding: 10px 0;
    background: #fff;
    position: relative;
    z-index: 111;
    width: 100%;
    overflow: hidden;
}
.info>dl>dt{
	overflow: hidden;
	margin: 0 auto;
	float: left;
	position: relative;
	width: 65px;
    padding: 5px 0;
    text-align: center;
}
.info>dl>dt>img{
	width: 45px;
    height: 45px;
}

.info>dl>dd{
	-webkit-box-flex: 1;
    box-flex: 1;
    font-size: 16px;
    padding-left: 10px;
    color: #3d3d3d;
}
.info>dl>dd>h5{
	padding: 5px 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-weight: normal;
}
.info>dl>dd>p{
	color: #7a7a7a;
    font-size: 12px;
    padding: 2px 0;
}
.info>dl>dd>p>span{
	color: #f25252;
    font-weight: bold;
    font-size: 16px;
    letter-spacing: 1px;
}
.logo{
	background-color: #fff;
    border-top: 1px solid #e6e6e6;
    border-bottom: 1px solid #e6e6e6;
    padding: 0px 10px 0px;
    margin: 10px 0;
}
.logo .reg_box{
	border-top: 1px solid #ededed;
    background-color: #fff;
    font-size: 16px;
    text-align: left;
    position: relative;
}

.logo .reg_box .msgCode{
	background-color: #f95554;
    position: absolute;
    right: 0;
    top: 0;
    display: inline-block;
    text-align: center;
    color: #fff;
    font-size: 14px;
    min-width: 85px;
    height: 40px;
    line-height: 10px;
}
.exit{
	text-align: center;
    color: #666;
    font-size: 12px;
    padding-bottom: 15px;
}
.exit>span{
	text-decoration: underline;
    color: #e53331;
}
.btn{
	position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    border-top: 1px solid #e0e0e0;
    z-index: 11;
}
.brand{
	
	padding-top: 6px;
}
</style>
<style>
.el-form-item__label{
	text-align: left;
	 font-size: 16px;
    color: #999;
    width: 70px;
    display: inline-block;
}
.el-input__inner{
	border: none;
}
.el-form-item{
	margin-bottom: 0px;
}
.el-radio__inner{
	display: none;
}
.el-radio{
	margin-right: 0;
}
.brand>.el-form-item__content{
	margin-left: 0 !important; 
}
</style>








