<template>
    <div class="container">

        <info_header></info_header>

        <div class='carBox'>

        <section class='car_img'>
            <img :src="'https://www.yixingauto.com/' + path"/>
        </section>
        <!--car_img end-->

        <section class='car_info'>
            <h2>{{index_text}}</h2>
            <div class='info_details'>
                <span class='money'><i>&yen;</i>{{index_ren}}</span>
                <i>保证金</i>
                <div class='time'>
                <label><i class='el-icon-tickets'></i><b>{{index_login}}</b>人报名</label>
                <label><i class='el-icon-time'></i>活动已结束</label>
                </div>
            </div>

            <info_promise></info_promise>
            
        <!--car_power end-->

        </section>
        <!--car_info end-->

        <section class='car_adviser'>
            <div class='adviser_img'>
                <img src='https://m.haoche.cn/images/zsgwicon.png'>
                <span><img src='https://m.haoche.cn/images/photo.png'/></span>
            </div>
            <div class='adviser_info'>
                <h6>{{adviser_name}}</h6>
                <p>{{adviser_company}}</p>
            </div>
            <div class='adviser_phone'>
                <a :href="'tel:' + adviser_phone"><img src='https://m.haoche.cn/images/dial.png'/></a>
                <a href='#'><img src='https://m.haoche.cn/images/weixin1.png'/></a>
            </div>
        </section>
        <!--car_adviser end-->

        <info_privilege></info_privilege>
        <!--car_privilege end-->

        <section class='car_news'>
            <h3><span></span>活动详情</h3>
            <p>{{news_info}}</p>
        </section>
        <!--car_news end-->

        <section class='car_dealer'>
            <h3><span></span>经销商信息</h3>
            <ul>
                <li>
                    <h4>{{adviser_company}}</h4>
                    <p>{{adviser_map}}</p>
                </li>
                <li>
                    <a :href="'tel:' + car_phone"><img src='https://m.haoche.cn/images/jxsphone.png' /></a></li>
            </ul> 
        </section>
        <!--car_dealer end-->

        <section class='car_flow'>
            <h3><span></span>参与流程</h3>
            <img :src='process'/>
        </section>
        <!--car_flow end-->

        <section class='car_hotline'>
            <p>
            <img src='https://m.haoche.cn/images/tel.png' />
            <label>4008080995</label>
            </p>
            <p>购车惠客服热线</p>

        </section>
        <!--car_hotline end-->

        <section class='car_footer'>
            <ul>
                <li>关于我们</li>
                <li>联系客服</li>
                <li>加入我们</li>
                <li>车展信息</li>
            </ul>
            <p>河北易之行网络科技有限公司<br>CopyRight © 2017 YixingAuto.com All Rights Reserved</p>
        </section>
        <!--car_footer end-->

        <section class='car_btn'>
            <ul>
                <li><i class='el-icon-service'></i><span>客服</span></li>
                <li><el-button type="danger" @click= 'Tosign'>立即报名</el-button></li>
            </ul>
        </section>

    </div>
    </div>
	
</template>


<script>
import info_header from '@/page/mobile/components/header'
import info_promise from '@/page/mobile/info/components/promise'
import info_privilege from '@/page/mobile/info/components/privilege'
import pic1 from '@/assets/img/info/process.jpg'
export default{
    components: {info_header,info_promise,info_privilege},

	data(){
		return{
            process: pic1,
            id: this.$route.params.id,
			index_text: '',
			index_ren: '',
			index_login: '',
			adviser_name: '何韵琳', //wu
			adviser_company: '',
			adviser_map: '',
			news_info: '' ,
            banner: '',
            adviser_phone: '', //wu
            car_phone: '',
            path:''
			// reads:[
			// {id: '1', title:'15万以内买自主SUV 这三款既有颜值又有内涵', src:'https://img.haoche.cn/cms/news/space/2019-04-09/20190409115037.jpg?x-oss-process=style/news_216', author: '车博会资讯'},
			// {id: '2', title:'想买自主品牌紧凑SUV 这三款顶配只需13万', src:'https://img.haoche.cn/cms/news/space/2019-01-30/20190130101951.jpg?x-oss-process=style/news_216', author:'车博会资讯'}
			// ],
		}
	},
    created(){
        this.GetInfo();
    },
    methods:{
        GetInfo:function(){
            this.$http({
                method: 'GET',
                url: 'https://www.yixingauto.com/groupon/Groupbuy/detail',
                params: {groupbuy_id: this.id}
            }).then(function(res){
                this.index_text = res.body.data.title;
                this.index_ren = res.body.data.pay_money;
                this.index_login = res.body.data.signnum;
                this.adviser_company = res.body.data.shop_name;
                this.adviser_map = res.body.data.address;
                this.news_info = res.body.data.group_desc;
                this.banner =  res.body.data.header_image;
                this.car_phone = res.body.data.shop_mobile;
                this.path = res.body.data.path
            },function(error){

            })
        },
        Tosign(){
            this.$router.push({path:'/fullList',query:{id:this.id, title:this.index_text, rmb: this.index_ren}});
        }
    }
}

</script>


<style scoped>
.carBox{
    position: relative;
    overflow: hidden;
    margin: 0 auto;
}
.carBox .car_img{
    width: 100%;
    overflow: hidden;
    position: relative;
    background-color: #fff;
}
.carBox .car_img>img{
    height: auto;
    display: inline;
    border: 0;
    -ms-interpolation-mode: bicubic;
    max-width: 100%;
    vertical-align: middle;
}
.car_info{
    background: #fff;
    position: relative;
    min-width: 300px;
    padding: 0.3rem 0.6rem;
    margin-top: 0.5rem;
}
.car_info>h2{
    font-size: 16px;
    color: #3d3d3d;
    margin-bottom: 3px;
    padding-top: 2px;
    padding-bottom: 10px;
    border-bottom: 1px solid #f1f1f1;
    padding: 2px 10px 10px;
}
.car_info .info_details{
    margin: 4px 10px 0;
    font-size: 14px;
    border-bottom: none;
}
.info_details .money{
    color: #ff5252;
    font-size: 26px;
}
.info_details .money>i{
    font-size: 16px;
    font-style: normal;
}
.info_details>i{
    color: #ccc;
    font-style: normal;
    font-size: 10px;
    display: inline-block;
    margin-left: 5px;
}
.info_details .time{
    margin-right: 0px;
    float: right;
    font-size: 12px;
    color: #333;
    padding: 5px 0 5px 0px;
}
.info_details .time>label{
    color: #808080;
    float: left;
    margin-top: 11px;
}
.info_details .time>label:nth-child(2){
    margin-left: 6px;
}
.info_details .time>label>i{
    display: inline-block;
    width: 18px;
    height: 16px;
    margin-right: 0px;
}
.info_details .time>label>b{
    color: #ff5252;
    font-style: normal;
}
/*car_img end*/


.car_adviser{
    background: #fff;
    position: relative;
    min-width: 300px;
    padding: 0rem 0.6rem;
    margin-top: 0.5rem;
    font-size: 14px;
    color: #000;
    height: 55px;
    display: box;
    display: -moz-box;
    display: -webkit-box;
}
.adviser_img{
    position: absolute;
    left: -5px;
    top: 0px;
    background-color: #ff6666;
    padding: 0 30px 0px 10px;
    height: 55px;
    line-height: 55px;
}
.adviser_img>img{
    width: 55px;
    border: 0;
    -ms-interpolation-mode: bicubic;
    max-width: 100%;
    vertical-align: middle;
}
.adviser_img>span{
    display: inline-block;
    height: 55px;
    width: 55px;
    border-radius: 100%;
    background-color: #ff6666;
    position: absolute;
    right: -27px;
    line-height: 53px;
    top: 0px;
    text-align: center;
}
.adviser_img>span>img{
    width: 40px;
    height: 40px;
    border-radius: 100%;
}
.adviser_info{
    position: absolute;
    left: 123px;
    bottom: 5px;
    font-size: 14px;
    color: #000;
    line-height: 20px;
}
.adviser_info>h6{
    font-size: 100%;
    font-weight: normal;
}
.adviser_info>p{
    color: #999;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: 12px;
}
.adviser_phone{
    position: absolute;
    right: 10px;
    bottom: 10px;
    font-size: 14px;
    color: #000;
    line-height: 20px;
}
.adviser_phone img{
        width: 32px;
    margin-left: 5px;
}
/* car_adviser end */



.car_news{
    background: #fff;
    position: relative;
    min-width: 300px;
    padding: 0rem 0.6rem;
    margin-top: 0.5rem;
}
.car_news>h3{
    font-size: 16px;
    padding: 7px 0 12px;
    display: block;
    width: 100%;
    border-bottom: 1px solid #ededed;
    margin-bottom: 5px;
    color: #000;
    font-weight: bold;
}
.car_news>h3>span{
    background-color: #F33B3A;
    margin-right: 7px;
    display: block;
    width: 3px;
    height: 20px;
    float: left;
    margin-top: 2px;
}
.car_news>p{
    text-align: center;
    padding: 0.6rem 0;
}
/*car_news end*/

.car_dealer{
    background: #fff;
    position: relative;
    min-width: 300px;
    padding: 0rem 0.6rem 0.6rem;
    margin-top: 0.5rem;
}
.car_dealer>h3{
    font-size: 16px;
    padding: 7px 0 12px;
    display: block;
    width: 100%;
    border-bottom: 1px solid #ededed;
    margin-bottom: 5px;
    color: #000;
    font-weight: bold;
}
.car_dealer>h3>span{
    background-color: #F33B3A;
    margin-right: 7px;
    display: block;
    width: 3px;
    height: 20px;
    float: left;
    margin-top: 2px;
}
.car_dealer>ul{
    overflow: hidden;
}
.car_dealer>ul>li{
    float: left;
    padding: 0 8px 0 0;
    border-right: 1px solid #f0f0f0;
}
.car_dealer>ul>li:nth-child(2){
     margin-top: 17px;
    float: right;
    margin-right: 10px;
}
.car_dealer>ul>li>h4{
    font-size: 14px;
    color: #646464;
}
.car_dealer>ul>li>p{
    font-size: 12px;
    color: #969696;
    margin-top: 6px;
}
.car_dealer>ul>li>a>img{
    width: 16px;
}
/*car_dealer end*/

.car_flow{
    background: #fff;
    position: relative;
    min-width: 300px;
    padding: 0rem 0.6rem;
    margin-top: 0.5rem;
}
.car_flow>h3{
    font-size: 16px;
    padding: 7px 0 12px;
    display: block;
    width: 100%;
    border-bottom: 1px solid #ededed;
    margin-bottom: 5px;
    color: #000;
    font-weight: bold;
}
.car_flow>h3>span{
    background-color: #F33B3A;
    margin-right: 7px;
    display: block;
    width: 3px;
    height: 20px;
    float: left;
    margin-top: 2px;
}
.car_flow img{
    border-top: 1px solid #e6e6e6;
    padding-top: 8px;
    border: 0;
    max-width: 100%;
    vertical-align: middle;
}
/*car_flow end*/

.car_hotline{
    background: #fff;
    position: relative;
    min-width: 300px;
    padding: 0.8rem 0.6rem 0.6rem;
    margin-top: 0.5rem;
}
.car_hotline>p{
    padding-bottom: 6px;
    text-align: center;
}
.car_hotline>p:nth-child(2){
    font-size: 12px;
    color: #000;
    display: block;
    height: 12px;
    line-height: 12px;
    color: #bdbdbd;
}
.car_hotline>p>img{
    width: 30px;
    vertical-align: bottom;
    margin-right: 5px;
}
.car_hotline>p>label{
    text-align: center;
    display: inline-block;
    height: 30px;
    line-height: 30px;
    font-size: 20px;
    color: #F33B3A;
}
/*car_hotline end*/

.car_read{
    background: #fff;
    position: relative;
    min-width: 300px;
    padding: 0.8rem 0.6rem 0.6rem;
    margin-top: 0.5rem;
}
.car_read>h3{
    font-size: 16px;
    padding: 7px 0 12px;
    display: block;
    width: 100%;
    border-bottom: 1px solid #ededed;
    margin-bottom: 5px;
    color: #000;
    font-weight: bold;
}
.car_read>h3>span{
    background-color: #F33B3A;
    margin-right: 7px;
    display: block;
    width: 3px;
    height: 20px;
    float: left;
    margin-top: 2px;
}
.car_read>ul{
    background: #fff;
    border-bottom: 1px solid #e1e2eb;
}
.car_read>ul>li{
    background: #fff;
    padding: 10px;
    border-top: 1px solid #e1e2eb;
    font-size: 17px;
}
.car_read>ul>li>div{
    flex-grow: 1;
    margin-right: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 67%;
    float: left;
}
.car_read>ul>li>img{
    width: 108px;
    height: 72px;
}
.car_read>ul>li>div>p{
    margin-left: 0;
