.car_footer{
    margin-top: 10px;
    color: #707070;
    margin: 0px 0 65px;
    font-size: 12px;
    padding: 0.6rem 0.6rem 0;
    text-align: center;
}
.car_footer>ul{
    margin: 0px 0 5px;
    display: inline-block;
}
.car_footer>ul>li{
    float: left;
    padding: 0.6rem 0.8rem 0.3rem;
}
.car_footer>p{
    line-height: 1rem;
}
.car_btn{
    position: fixed;
    bottom: 0;
    left: 0;
