<template>
	<div class='header'>
  	<span class="back-btn" @click="goBack()">←</span>
  	<h6 class="header-name" v-text="title"></h6>
  </div>
</template>

<style scoped>
.header{
	max-width: 640px;
	position: relative;
	background: -webkit-linear-gradient(left,#d7313a,#fc3b3f);
	background: #ff3938;
	color: #fff;
	height: 2.55rem;
	line-height: 2.55rem;
}
.header h6{
	width: 80%;
	text-align: center;
	font-size: 1.1rem;
	letter-spacing: 1px;
	font-weight: normal;
	color: #fff;
}
.header span{
	display: block;
	width: 10%;
	float: left;
	text-align: center;
}
</style>

<script>
	export default{
		data(){
			return{
				title: '易行汽车'
			}
		},
		methods:{
			goBack(){
   	  	 history.back(-1);
   	  	}
		}
	}
</script>


