<template>
	<div class="info">
		<h5>{{title}}</h5>
		<p>需要支付金额:<span>{{rmb}}</span></p>
		<p>选择支付方式</p>
		<div class="radio">
			<el-radio v-model="radio" label="weixin" >
					<i class="iconfont icon-weixin"></i>
					<div class="list">
						<h5>微信支付</h5>
						<span>推荐安装最新版微信使用</span>
					</div>
			</el-radio>
			<el-radio v-model="radio" label="zhifubao">
					<i class="iconfont icon-zhifubao"></i>
					<div class="list">
						<h5>支付宝</h5>
						<span>推荐有支付宝账户的用户使用</span>
					</div>
			</el-radio>
		</div>

		<div class="btn">
			<el-button type="danger" style='width:100%' @click='Compay'>确认支付</el-button>
		</div>

		
		
	</div>
</template>

<script>
	export default{
		data(){
			return{
				title: this.$route.query.title,
				rmb: this.$route.query.rmb,
				trade_no: this.$route.query.trade_no,
				radio: 'weixin',
				html:''
			}
		},
		methods:{
			Compay(){
				let data = {'trade_no':this.trade_no}
				this.$axios.post('https://www.yixingauto.com/groupon/pay/alipay_wap',data).then((res)=>{

                    this.html = res.data
                    var form= res.data;

                    const div = document.createElement('div') //创建div
                    div.innerHTML = form//此处form就是后台返回接收到的数据
                    document.body.appendChild(div)
                    document.forms[0].submit()

				})
			}
		}
	}
</script>

<style scoped>
@import '//at.alicdn.com/t/font_1180562_mv7ppqpb6c.css';
.info{
	margin: 0 auto;
	position: relative;
	overflow: hidden;
	padding: 6px 8px;
}
.info>h5{
	font-size: 16px;
    font-weight: normal;
    padding-top: 11px;
}
.info>p{
	line-height: 28px;
}
.info>p>span{
	color: #f25252;
	padding: 0 6px;
	font-weight: bold;
	font-size: 16px;
}
.radio{
	background: #fff;
	position: relative;
	overflow: hidden;
	padding: 6px 0;
}
.radio>label{
	display: block;
	width: 100%;
	float: left;
	padding-left: 10px;
	line-height: 28px;
}
.radio>label:nth-child(2){
	border-top: 1px solid #ccc;
}
.icon-weixin{
	font-size: 20px;
	color: #2aaf90;
	display: block;
	float: left;
	padding-right: 8px;
	padding-top: 12px;
}
.icon-zhifubao{
	font-size: 20px;
	color: #4497f4;
	display: block;
	float: left;
	padding-right: 8px;
	padding-top: 12px;
}
.list{
	display: block;
	float: left;
}
.btn{
	position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    border-top: 1px solid #e0e0e0;
    z-index: 11;
}
</style>
<style>
.el-radio+.el-radio{
	margin-left: 0px;
}
.el-radio__input{
	position: absolute;
	right: 10px;
	bottom: 3px;
}
</style>




