<template>
	<div class="taps">
		<el-tabs v-model="activeName2" type="card" @tab-click="handleClick">

	         <el-tab-pane label="全部" name="all" class='tab_li'>
	         	<all_list></all_list>	
	         </el-tab-pane>

	         <el-tab-pane label="待支付" name="wait" class='tab_li'>
				<wait_list></wait_list>
	         </el-tab-pane>

	          <el-tab-pane label="已支付" name="already" class='tab_li'>
	         	<already_list></already_list>	
	         </el-tab-pane>

	         <el-tab-pane label="已消费" name="complete" class='tab_li'>
				<complete_list></complete_list>
	         </el-tab-pane>

        </el-tabs>
	</div>
</template>

<style scoped>
#tab-act_ing{
	width: 50%;
	text-align: center;
}
#tab-act_end{
	width: 50%;
	text-align: center;
}
</style>

<style>
.el-tabs__nav{
	width: 100%;
	overflow: hidden;
	position: relative;
	display: block;
}
.el-tabs__header{
	background: #fff;
}
#tab-all,#tab-wait,#tab-already,#tab-complete{
	width: 25%;
	text-align: center;
}
</style>

<script>
import all_list from '@/page/mobile/order/components/all'
import wait_list from '@/page/mobile/order/components/obligation'
import already_list from '@/page/mobile/order/components/consumed'
import complete_list from '@/page/mobile/order/components/paid'
	export default{
		components: {all_list,wait_list,already_list,complete_list},
		data(){
			return{
				activeName2: 'all'
			}
		},
		methods:{
			handleClick(){

			}
		}
	}
</script>


