import Vue from 'vue'
import Router from 'vue-router'
/*引入页面*/

//手机页面
import carList from '@/page/mobile/info/info' //团购详情页
import activity from '@/page/mobile/activity/activity' //活动列表页
import loginList from '@/page/Login' //用户登陆页面
import registerList from '@/page/Register' //用户注册
import passwordList from '@/page/Password'  //找回密码
import orderList from '@/page/mobile/order/order'  //订单页面
import paymentList from '@/page/mobile/payment/payment'  //支付页面
import fullList from '@/page/mobile/Full/full' //提交订单页面

import Index from '@/pc_page/index'
import news from '@/pc_page/news'
import temaihui from '@/pc_page/temaihui'
import xuanche from '@/pc_page/xuanche'

Vue.use(Router)

export default new Router({
	//在创建的 router 对象中，如果不配置 mode，就会使用默认的 hash 模式，该模式下会将路径格式化为 #! 开头
	mode: 'history',
  routes: [
    {
      path: '/',
      name: 'activity',
      component: activity
    },
    {
      path: '/carList/:id',
      name: 'carList',
      component: carList
    },
    
    {
      path: '/loginList',
      name: 'loginList',
      component: loginList
    },
    {
      path: '/registerList',
      name: 'registerList',
      component: registerList
    },
    {
      path: '/passwordList',
      name: 'passwordList',
      component: passwordList
    },
    {
      path: '/orderList',
      name: 'orderList',
      component: orderList
    },
    {
      path: '/paymentList',
      name: 'paymentList',
      component: paymentList
    },
    {
      path: '/fullList',
      name: 'fullList',
      component: fullList
    },
    {
      path: '/Index',
      name: 'Index',
      component: Index
    },
    {
      path: '/news',
      name: 'news',
      component: news
    },
    {
      path: '/temaihui',
      name: 'temaihui',
      component: temaihui
    },
    {
      path: '/xuanche',
      name: 'xuanche',
      component: xuanche
    }
    
  ]
})



