<template>
	<div class="wrap">
		<pc_header></pc_header>

			<el-carousel arrow="never">
		   <el-carousel-item v-for="item in items" :key='item.id'>
		      <img :src='item.src'>
		   </el-carousel-item>
		 </el-carousel>

		 <div class="news">
		 	<el-tabs v-model="activeName" @tab-click="handleClick">
    			<el-tab-pane label="车市资讯" name="first">
    				<ul>
    					<li v-for='(item,aid) in list' :key="aid">
    						<router-link :to="'/Content/'+ item.aid">{{item.title}}</router-link>
    					</li>
    				</ul>
    			</el-tab-pane>
    			<el-tab-pane label="活动预告" name="second">活动预告</el-tab-pane>
    			<el-tab-pane label="商家资讯" name="third">商家资讯</el-tab-pane>
    			<el-tab-pane label="往期回顾" name="fourth">往期回顾</el-tab-pane>
  			</el-tabs>
		 </div>

		<pc_footer></pc_footer>
	</div>
</template>

<style scoped>
.el-carousel{
	height: 90px;
    max-width: 1200px;
    overflow: hidden;
    background: #f13833;
    display: block;
    position: relative;
    margin: 0 auto;
}
.el-carousel img{
	width: 100%;
	height: 90px;
}
.news{
	max-width: 1200px;
	overflow: hidden;
	position: relative;
	margin: 0 auto;
}
</style>

<script>
	import pc_header from '@/components/pcComponents/Header'
	import pc_footer from '@/components/pcComponents/Footer'
	export default{
		components: {pc_header,pc_footer},
		data(){
			return{
				items:[
					{id: 0, src:'https://sjz.yixingauto.com/uploads/images/20190410/35cffedc6a135bb4d6b34b242f4451e1.jpg'},
					{id: 1, src: 'https://sjz.yixingauto.com/uploads/images/20190419/b19eafbb9902ee859a1a50a3189fbaef.jpg'},
					{id: 2, src: 'https://zz.yixingauto.com/uploads/images/20190412/0cff25a0e77145fb9f8ccf69e193ef13.jpg'}
				],
				activeName: 'first',
				list:[
				{aid: 0, item: '新闻标题一'},
				{aid: 1, item: '新闻标题一'},
				{aid: 2, item: '新闻标题三'},
				{aid: 3, item: '新闻标题四'}
				]
			}
		},
		methods:{
			handleClick(tab, event) {

			}
		}
	}
</script>


