<template>
	<div class="container">
		<lg_header></lg_header>
		<div class='register'>
		<h6>全国共有<b>1119784</b>个车友加入易行汽车</h6>

		<div class='reg_wrap'>

			<el-form :model='registerForm' :rules="rules" ref="registerForm" label-width="100px" status-icon>

			<el-form-item label=' ' prop="name" class='reg_box'>
				<el-input type="text" placeholder="请输入姓名" v-model='registerForm.name'></el-input>
			</el-form-item>

			<el-form-item label=' ' class='reg_box' prop='phone'>
				<el-input type="text" placeholder="请输入手机号" v-model='registerForm.phone'></el-input>
				<el-button @click="msgCode('phone')" class='msgCode'>获取验证码</el-button>
			</el-form-item>

			<el-form-item label=' ' class='reg_box' prop='code'>
				<el-input type='text' placeholder="请输入短信验证码" v-model='registerForm.code'></el-input>
			</el-form-item>

			<el-form-item label=' ' class='reg_box' prop='pass'>
				<el-input type='password' placeholder="密码(6-20个数字或字符)" v-model='registerForm.pass'></el-input>
			</el-form-item>

			<el-form-item label=' ' class='reg_box' prop='repass'>
				<el-input type='password' placeholder="请确认密码" v-model='registerForm.repass'></el-input>
			</el-form-item>

			<el-form-item label=' ' class='reg_btn'>
				<el-button type="" @click="reg_value('registerForm')" class='reg_btnbutton'>提交</el-button>
			</el-form-item>

			</el-form>

			<p class='message'>收不到短信？使用<a href='#'>语音验证码</a></p>
			<div class='reg_serv'>
				<p>如有疑问请联系客服：<b>400-808-1710</b></p>
				<p>客服服务时间：周一至周五9:00-22:00</p>
			</div>

		</div>
	</div>
	</div>
	
</template>



<style scoped>
.register{
	margin-top: 40px;
}
.register>h6{
	font-size: 16px;
    text-align: center;
    padding-bottom: 20px;
    font-weight: normal;
}
.register>h6>b{
	color: #f25252;
    margin: 0 5px;
    display: inline-block;
    font-size: 18px;
}
.reg_wrap{
	max-width: 640px;
	height: auto;
	position: relative;
	overflow: hidden;
	margin: 0 auto;
	text-align: center;
	padding: 16px 0;
}
.reg_wrap .reg_box{
	border-top: 1px solid #ededed;
    background-color: #fff;
    font-size: 16px;
    text-align: left;
    position: relative;
}

.reg_wrap .reg_box .msgCode{
	background-color: #f95554;
    position: absolute;
    right: 0;
    top: 0;
    display: inline-block;
    text-align: center;
    color: #fff;
    font-size: 14px;
    min-width: 85px;
    height: 40px;
    line-height: 10px;
}

.reg_wrap .reg_btn{
	margin: 20px auto;
	display: block;
}
.reg_wrap .reg_btn .reg_btnbutton{
	color: #fff;
    display: block;
    background-color: #f95554;
    padding: 13px 0;
    text-align: center;
    margin: 20px auto;
    font-size: 16px;
    border: 0;
    width: 90%;
    border-radius: 3px;
}
.reg_wrap>.message{
	text-align: center;
    margin: 7px;
    font-size: 13px;
}
.reg_wrap>.message>a{
	color: #f25252;
    text-decoration: underline;
}
.reg_wrap>.reg_serv{
	border: 1px solid #ccc;
    background: -webkit-gradient(linear, left top, left bottom, from(#fff), to(#f5f5f5));
    text-align: center;
    padding: 8px 0;
    line-height: 18px;
    margin: 30px 15px 10px;
    border-radius: 5px;
    color: #808080;
}
.reg_wrap>.reg_serv>p{
	font-size: 12px;
	line-height: 1.45rem;
}
.reg_wrap>.reg_serv>p>b{
	color: #f95554;
}

</style>

<style>
.reg_box .el-form-item__label{
	box-shadow: 1px 0px 5px rgba(0,0,0,0.1 );
    display: inline-block;
    width: 40px !important;
    height: 40px;
    vertical-align: bottom;
    text-align: center;
    font-size: 14px;
    color: #969696;
}
.reg_box .el-form-item__content{
	margin-left: 0 !important;
}
.reg_btn .el-form-item__content{
	margin-left: 0 !important;
}
.reg_box .el-input{
	width: 90%;	
}
.reg_box .el-input__inner{
	margin-left: 10px;
    border-left: 1px solid #e6e6e6;
    border: 0;
    width: 100%;
}
.reg_box .el-form-item__error{
	color: #fa5555;
    font-size: 12px;
    line-height: 1;
    padding-top: 4px;
    position: absolute;
    top: 97%;
    left: 13%;
}
.reg_box:nth-child(2) .el-input{
	width: 63%;
}
</style>


<script>
import lg_header from '@/components/lgComponents/Header'
export default{
	components: {lg_header},
	data(){

		var validateName = (rule, value, callback) =>{
       		if(value ===''){
       			callback('请输入姓名');
       		}else{
       			callback();
       		}
       	};

       	var validatePhone = (rule, value, callback) =>{
       		if(value ===''){
       			callback('请输入手机号');
       		}else{
       			const reg = /^(((13[0-9]{1})|(14[4,7]{1})|(15[0-3,5-9]{1})|(17[0,1,3,5-8]{1})|(18[0-9]{1})|(19[8,9]{1})|(166))+\d{8})$/
       			if(reg.test(value)){
       				let mobile = {'mobile' : value};
       				console.log(mobile);
       				this.$http.post('http://192.168.3.250/yixingauto/public/groupon/login/checkmobile',mobile).then((res)=>{
       					if(res.body.code==200){
       						callback();
       					}else{
       						callback('手机号已注册');
       					}
       				});
       			}else{
       				return callback(new Error('请输入正确的手机号'));
       			}
       		}
       	};

       	var validateMsg = (rule, value, callback)=>{
       		if(value ===''){
       			callback('请输入短信验证码');
       		}else{
       			callback();
       		}
       	};

       	var validatePass = (rule, value, callback)=>{
       		if(value ===''){
       			callback('请输入密码');
       		}else{
       			const pas = /[0-9a-zA-Z]{6,20}/
       			if(pas.test(value)){
       				callback();
       			}else{
       				callback('密码由6-20位数字和字母组合');
       			}
       			callback();
       		}
       	};

       	var validateRepass = (rule, value, callback)=>{
       		if(value ===''){
       			callback('请再次输入密码');
       		}else if(value != this.registerForm.pass){
       			callback('两次输入密码不一致!');
       		}else{
       			callback();
       		}
       	};


		return{
			registerForm:{
				name: '',
				phone: '',
				code: '',
				pass: '',
				repass: ''
			},
			rules:{
				name:[
				{validator: validateName, trigger: 'blur'}
				],
				phone:[
				{validator: validatePhone, trigger: 'blur'}
				],
				code:[
				{validator: validateMsg, trigger: 'blur'}
				],
				pass:[
				{validator: validatePass, trigger: 'blur'}
				],
				repass:[
				{validator: validateRepass, trigger: 'blur'}
				]
			}

		}
	},

	methods:{
		reg_value(formName){
			this.$refs[formName].validate((valid)=>{
			if(valid){
				let data = {'name':this.registerForm.name,'mobile':this.registerForm.phone,'pwd':this.registerForm.pass,'repwd':this.registerForm.repass,'verifycode':this.registerForm.code};
				console.log(data);
				this.$http.post('https://www.yixingauto.com/groupon/login/register',data).then((res)=>{
					if(res.body.code==200){	
						setTimeout(()=>{
      	 						this.$message({
      	 						type: 'success',
      	 						message: '恭喜您，注册成功',
      	 						});
      	 						setTimeout(()=>{
      	 						this.$router.push('/');
      	 						},500)
      	 						},500);

						}else{
							setTimeout(()=>{
	      	 						this.$message({
	      	 						type: 'error',
	      	 						message: res.body.msg,
	      	 						})
	      	 					},500);
					}
				})
			}else{
				console.log('error')
			}

		})
		},
		msgCode(){
			let codeData = {'mobile':this.registerForm.phone};
			this.$http.post('https://www.yixingauto.com/groupon/login/sendverifycode',codeData).then((res)=>{
				if(res.body.code==200){
					let time = 60
			        this.buttonText = '已发送'
			        this.isDisabled = true
			        if(this.flag){
			        	this.flag = false;
			        	let timer = setInterval(() =>{
			        		time--;
			        		this.buttonText = time + '秒'
			        		if(time === 0){
			        			clearInterval(timer);
			        			this.buttonText = '重新获取'
					            this.isDisabled = false
					            this.flag = true;
			        		}
			        	},1000)
			        }
				}else{
					console.log(res.body.msg);
				}
			})

		}

	}
}

</script>


