<template>
  <div class='header'>
  	<div class="logo">
  		<img src="http://tj.yixingauto.com/static/groupon/pc/images/logo.png" alt="">
  	</div>
  	<div class="city">
  		<i class="el-icon-location-outline"></i>
  		<span class="city_name">{{city_name}}</span>
  	</div>
  	<ul>
  		<li @click="Toindex">首页</li>
  		<li @click='Totemaihui'>新车特卖会</li>
  		<li @click='Toxuanche'>选车</li>
  		<li @click="Tonews">资讯</li>
  	</ul>
  </div>
</template>


<style scoped>
.header{
	position: relative;
	max-width: 1200px;
	margin-left: auto;
	margin-right: auto;
	padding-left: 0.9375rem;
	padding-right: 0.9375rem;
	height: 50px;
    line-height: 50px;
    margin-top: 15px;
    margin-bottom: 20px;
}
.logo{
	overflow: hidden;
    float: left;
    margin: 7px 0;
}
.logo>img{
	vertical-align: top;
    max-height: 43px;
    display: block;
}
.city{
	font-size: 0.875rem;
    float: left;
    line-height: 30px;
    padding-left: 10px;
    position: relative;
    top: 18px;
    margin-left: 10px;
    height: 30px;
}
.city>i{
	color: #FF0000;
	position: relative;
    display: inline-block;
    font: normal normal normal 14px/1 iconfont;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
}
.city>span{
	text-decoration: underline;
    cursor: pointer;
}
.header>ul{
	padding-top: 10px;
    margin-left: 10px;
    padding-left: 90px;
    overflow: hidden;
    border-bottom: none;
    background: none;
}
.header>ul>li{
	position: relative;
    float: left;
    margin-left: 55px;
    cursor: pointer;
    font-size: 16px;
    font-weight: 700;
    display: block;
    color: #666;
    line-height: 38px;
}
.header>ul>li:hover{
	color: #ce1d2b;
	background: none;
}
</style>


<script>
 	export default{

 		data(){
 			return{
 				city_name: '石家庄',
 				menus:[
 				{id: 0, title: '首页'},
 				{id: 1, title: '新车特卖会'},
 				{id: 2, title: '选车'},
 				{id: 3, title: '特卖会资讯'}
 				]
 			}
 		},
 		methods:{
 			Tonews(){
 				this.$router.push('/news');
 			},
 			Totemaihui(){
 				this.$router.push('/temaihui');
 			},
 			Toxuanche(){
 				this.$router.push('/xuanche');
 			},
 			Toindex(){
 				this.$router.push('/Index');
 			}
 		}
 	}
</script>






