<template>
	<div class='header'>
	  	<span class="back-btn" @click="goBack()">←</span>
	  	<h6 class="header-name" v-text="title"></h6>
  </div>
</template>

<script>
 export default{
   data(){
   	  return{
   	  	title: '填写订单'
   	  }
   },
   methods:{
   	  goBack(){
   	  	 history.back(-1);
   	  },
   	  LoginBtn(){
      	 	this.$router.push('/loginList');
      	 },
   }
 }
</script>

<style scoped>
  .header{
	max-width: 640px;
	position: relative;
	background: #fff;
	height: 2.55rem;
	line-height: 2.55rem;
}
.header h6{
	width: 70%;
	font-weight: normal;
	text-align: center;
	font-size: 1.1rem;
	letter-spacing: 1px;
}
.header span{
	display: block;
	width: 10%;
	float: left;
	text-align: center;
}
/*header end*/
</style>


