<template>
	<div>
		<div v-for='item in con_list'>
			<div class="obligation">
			<div class="title">
			<span>{{item.title}}</span>
			<span>{{item.state}}</span>
		</div>
		<router-link :to="'/carList/' + item.id">
			<div class="infos">
				<img :src="'https://www.yixingauto.com/' + item.path">
				<div class="info">
					<h5>{{item.info_title}}</h5>
					<span>活动时间：{{item.begin_time}}-{{item.end_time}}</span>
					<p><i>&yen;</i>{{item.pay_money}}</p>
				</div>
			</div>
		</router-link>
		<el-collapse accordion>
			<el-collapse-item title="报名信息">
				<div class="details">
					<p>姓&nbsp;&nbsp;名：<span>&nbsp;{{item.user_name}}</span></p>
					<p>电&nbsp;&nbsp;话:<span>&nbsp;{{item.mobile}}</span></p>
					<p>意向车型:<span>&nbsp;{{item.name}}</span></p>
				</div>
			</el-collapse-item>
		</el-collapse>
	</div>
	</div>
	</div>
		
</template>

<style scoped>
.obligation{
	margin: 10px auto;
	background: #fff;
	padding: 4px 8px;
	overflow: hidden;
}
.title{
	width: 100%;
	margin: 0 auto;
	overflow: hidden;
	height: 38px;
	line-height: 38px;
	border-bottom: 1px solid #ccc;
	color: #999;
}
.title>span{
	float: left;
}
.title>span:nth-child(2){
	float: right;
	color: #f2ca52;
}
.infos{
	background: #fafafa;
	overflow: hidden;
	margin: 0 auto;
	position: relative;
	padding: 12px 0;
}
.infos>img{
	float: left;
	width: 150px;
	height: 100px;
}
.infos>.info{
	float: left;
	padding-left: 16PX;
}
.infos>.info>h5{
	font-size: 16px;
	font-weight: normal;
	line-height: 0;
	padding-top: 11px;
}
.infos>.info>span{
	font-size: 14px;
	color: #ccc;
}
.infos>.info>p{
	line-height: 0;
	padding-top: 30px;
	color: #999;
}
.details{
	border-bottom: 1px solid #ccc;
	padding-bottom: 6px; 
}
.details>p{
	line-height: 32px;
}

</style>


<script>
import {setCookie,getCookie} from '@/assets/js/cookieUtil'
	export default{
		data(){
			return{
				con_list:[]
			}
		},
		created(){
			this.GetInfo();
		},
		methods:{
			GetInfo(){
				this.phone = getCookie("phone");
				var data = {'mobile':this.phone};
				this.$axios.post('https://www.yixingauto.com/groupon/Groupbuy/center_order_list',data).then((res)=>{
						this.con_list = res.data.data3;
						console.log(this.obli_list)
				})
			},
			Topay(){

			}
		}
		
	}
</script>


