<template>
  
    <div class='container'>
      <lg_header></lg_header>
    	<div class='logo'>
    		<img src="http://www.yixingauto.com/static/groupon/pc/images/logo.png" alt="">
    	</div>

    	<div class='login'>
    		<el-form :model='ruleForm' :rules="rules" ref="ruleForm" label-width="100px" status-icon :label-position="labelPosition">

    		<el-form-item label="账户" prop="phone" class='input_box'>
    			<el-input v-model='ruleForm.phone' placeholder="请输入手机号"></el-input>
    		</el-form-item>

    		<el-form-item label="密码" prop="pass" class='input_box'>
    			<el-input type='password' v-model='ruleForm.pass' placeholder="请输入密码"></el-input>
    		</el-form-item>

    		<el-form-item class='btn'>
    			<el-button @click="submitForm('ruleForm')">登录</el-button>
    		</el-form-item>

    		<p class='nots'>
			  <span @click='ToPassword'>忘记密码</span>
			  <span @click='ToRegister'>快速注册</span>
			</p>

    	</el-form>
    	</div>
    	
    	
    	
    	
    </div>
</template>

<style scoped>

.logo{
	max-width: 640px;
	height: auto;
	position: relative;
	overflow: hidden;
	margin: 20px auto;
	display: block;
	text-align: center;
	margin-top:40px;
}
.logo>img{
	width: 46%;
	height: 46%;
	margin: 0 auto;
}
/*logo end*/

.login{
	max-width: 640px;
	height: auto;
	position: relative;
	overflow: hidden;
	margin: 0 auto;
	padding: 20px 0;
}
.login .input_box{
	background: #fff;
	height: 2.6rem;
	line-height: 2.6rem;
	border: 1px solid #e6e6e6;
	position: relative;
	text-align: center;
	margin-bottom: 22px;
}
.login .input_box:nth-child(2){
	border-top: none;
}

.login .btn{
	margin: 20px auto;
	display: block;
	text-align: center;
}
.login .btn button{
	width: 90%;
	background: #F95454;
	border-radius: 6px;
	color: #fff;
	border: none;
	height: 2.6rem;
	line-height: 2.6rem;
	font-size: 1rem;
	padding: 0;
}
.nots{
	margin: 0 auto;
}
.nots>span{
	padding: 0 60px;
	cursor: pointer;
}
</style>



<style>
.input_box>.el-form-item__label{
	display: block;
	float: left;
	border-right: 1px solid #ccc;
	text-align: center;
	font-size:16px;
	}
.input_box .el-input__inner{
	border: none;
	text-align: left;
	float: left;
	line-height: 2.6rem;
	padding-left: 1rem;
}
.btn .el-form-item__content{
	margin-left: 0 !important;
}

.input_box .el-form-item__error{
	color: #fa5555;
    font-size: 12px;
    line-height: 1;
    padding-top: 4px;
    position: absolute;
    top: 75%;
    left: 2%;
}
</style>

<script>
import lg_header from '@/components/lgComponents/Header'
import {setCookie,getCookie} from '@/assets/js/cookieUtil'
    export default{
      components: {lg_header},
       data(){

       	var validatePhone = (rule, value, callback) => {
       		if(value === ''){
       			callback('请输入手机号');
       		}else{
       			const reg = /^(((13[0-9]{1})|(14[4,7]{1})|(15[0-3,5-9]{1})|(17[0,1,3,5-8]{1})|(18[0-9]{1})|(19[8,9]{1})|(166))+\d{8})$/
       			console.log(reg.test(value));
       			if(reg.test(value)){
       				callback();
       			}else{
       				return callback(new Error('请输入正确的手机号'));
       			}
       		}
       	};

       	var validatePass = (rule, value, callback) =>{
       		if(value ===''){
       			callback('请输入密码');
       		}else{
       			callback();
       		}
       	};


       	return{
       		ruleForm: {
       			phone: '',
       			pass: ''
       		},
       		labelPosition: 'left',
       		rules:{
       			phone:[
       			{validator: validatePhone, trigger: 'blur'}
       			],
       			pass:[
       			{validator: validatePass, trigger: 'blur'}
       			]
       		}
       	}
       
       },

       methods: {
      	 submitForm(formName){
      	 	this.$refs[formName].validate((valid)=>{
      	 		if(valid){
      	 			let data = {'mobile':this.ruleForm.phone,'pwd':this.ruleForm.pass};
      	 			this.$http.post('https://www.yixingauto.com/groupon/login/login',data).then((res)=>{
      	 				if(res.body.code==200){
                  
                  setCookie('phone',this.ruleForm.phone);
                  setCookie('name',res.body.data.name);

      	 					setTimeout(()=>{
      	 						this.$message({
      	 						type: 'success',
      	 						message: '登录成功',
      	 						});
      	 						setTimeout(()=>{
      	 						history.back(-1);
      	 						},1000)
      	 						},100);
                  
      	 					
      	 				}else{
      	 					setTimeout(()=>{
      	 						this.$message({
      	 						type: 'error',
      	 						message: res.body.msg,
      	 						})
      	 					},500);
      	 				}
      	 			})
      	 		}
      	 	})

      	 },
      	 ToPassword(){
      	 	this.$router.push('/passwordList');
      	 },
      	 ToRegister(){
      	 	this.$router.push('/registerList');
      	 },

  }
  }
</script>


