<template>
	<div>
		<router-link v-for='item in list' :to="'/carList/' + item.id">
			<div class="list" >
				<div class="left">
					<h5>{{item.title}}</h5>
					<img :src="'https://www.yixingauto.com/' + item.header_image">
				</div>

				<div class="right">
					<img :src="'https://www.yixingauto.com/' + item.brand_image">
					<div class="info">
						<p>{{item.signup_number}}人报名</p>
						<div class="rmb">
							<i>&yen;</i>{{item.pay_money}}
						</div>
						<p>{{item.time}}</p>
					</div>
				</div>
			</div>
	</router-link>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				list:[]
			}
		},
		created(){
			this.GetInfo();
		},
		methods:{
			GetInfo(){

				this.$axios.post('https://www.yixingauto.com/groupon/Groupbuy/order_list').then((res) =>{
					this.list = res.data.data1;
					// let url = 'https://www.yixingauto.com/'
					// let set = []
					// if(res.data != undefined){
					// for(let i =0; i<res.data.data1.length; i++){
					// 	let groupinfo = {}
					// 	let data_url = res.data.data1[i]
					// 	 groupinfo.id = data_url.id
					// 	 groupinfo.title = data_url.title
					// 	 groupinfo.name = data_url.signup_number
					// 	 groupinfo.img = url + data_url.header_image
					// 	 groupinfo.logo = url + data_url.brand_image
					// 	 groupinfo.rmb = data_url.pay_money
					// 	 groupinfo.time = data_url.time
					// 	 set[i] = groupinfo
					// }
					// }
					// this.list = set
				})
			}
		}
	}
</script>

<style scoped>
.list{
	border: 1px solid #dadbdd;
    border-top-right-radius: 20px;
    margin: 0 8px 10px;
    background: #fff;
    display: box;
    display: -moz-box;
    display: -webkit-box;
    color: #333;
    position: relative;
}
.list>.left{
	-webkit-box-flex: 1;
    box-flex: 1;
    position: relative;
    padding-bottom: 5px;
}
.list>.left>h5{
	padding: 7px 0 5px;
    font-size: 16px;
    display: block;
    margin-left: 10px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-top: 3px;
}
.list>.left>img{
	display: inline;
	width: 100%;
}
.list>.right{
	width: 75px;
    padding: 10px 0;
    text-align: center;
}
.list>.right>img{
	width: 60%;
    overflow: hidden;
    margin: 0 auto 24%;
}
.list>.right>.info{
	position: absolute;
    bottom: 1px;
    width: 75px;
    right: 0px;
}
.list>.right>.info>p{
	font-size: 12px;
	text-align: center;
    color: #666666;
    line-height: 24px;
}
.list>.right>.info>.rmb{
	background: #db313a;
    text-align: center;
    margin: 5px -6px 0px 0;
    padding: 3px 6px 3px 0;
    color: #fff;
}
</style>




